<template>
  <div>
    <div class="text-center">
      <div class="btn-group" role="group">
        <button v-for="row in list" type="button" class="btn btn-outline-dark"
                v-bind:class="{'active': selectedDate.includes(row.value)}" @click="changeDay(row.value)">{{row.display}}</button>
      </div>
    </div>
    <div class="text-center">
      <div class="btn-group" role="group">
        <button type="button" class="btn btn-outline-primary"
                v-bind:class="{'active': selectedMealType === '1'}" @click="changeMealType('1')">
          조식
        </button>
        <button type="button" class="btn btn-outline-primary"
                v-bind:class="{'active': selectedMealType === '2'}" @click="changeMealType('2')">
          중식
        </button>
        <button type="button" class="btn btn-outline-primary"
                v-bind:class="{'active': selectedMealType === '3'}" @click="changeMealType('3')">
          석식
        </button>
      </div>
    </div>

    <div v-if="this.data.length === 0" class="alert alert-danger text-center" role="alert">
      {{this.getRestName(this.$route.params.code)}} 식당은 {{this.getMealTypeName(this.$route.params.mealType)}}을 제공하지
      않습니다.
    </div>
    <div class="container">
      <div class="row justify-content-center">
        <div class="card col-5 col-sm-5 col-md-3 col-lg-3 col-xl-2 card-container" v-for="row in data" >
          <img class="card-img-top" :src="row.imgUrl" alt="Card image cap">
          <div class="card-body">
            <div class="card-title">{{ row.title }}</div>
            <p class="card-text">{{ row.sub }}</p>
            <p class="card-text">{{ row.cal }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>

import axios from 'axios';
import moment from 'moment';
import Constants from '../constants';

export default {
  data () {
    return {
      data: {},
      list: [],
      selectedDate : this.$route.params.date,
      selectedMealType : this.$route.params.mealType
    }
  },
  watch:{
    $route () {
      axios
        .get(Constants.ENDPOINT + '/menu?restCode=' + this.getRestParsedCode(this.$route.params.code) + '&date=' + this.$route.params.date
        + '&mealType=' + this.$route.params.mealType)
        .then(response => {
          this.data = response.data;
        });
    },
  },
  mounted () {
    axios
      .get(Constants.ENDPOINT + '/menu?restCode=' + this.getRestParsedCode(this.$route.params.code) + '&date=' + this.$route.params.date
            + '&mealType=' + this.$route.params.mealType)
      .then(response => {
        this.data = response.data;
      });

    for (let i = -2; i < 4; i++) {
      let data = {};
      if (i === 0) {
        data.display = 'Today';
      } else {
        data.display = moment().add(i, 'days').format('D') + '/' + this.getDayName(moment().add(i, 'days').day());
      }
      data.value = moment().add(i, 'days').format('YYYYMMDD');
      this.list.push(data);
    }
  },
  methods : {
    load: function(mealType) {
      axios
        .get(Constants.ENDPOINT + '/menu?restCode=' + this.getRestParsedCode(this.$route.params.code) + '&date=' + this.$route.params.date
                + '&mealType=' + mealType)
        .then(response => {
          this.data = response.data;
        });
    },
    getRestName: function(code) {
      return Constants.REST_NAME_MAP[code]['name'];
    },
    getRestParsedCode: function(code) {
      return Constants.REST_NAME_MAP[code]['code'];
    },
    getDayName: function(i) {
      switch(i) {
        case 0:
          return '일';
        case 1:
          return '월';
        case 2:
          return '화';
        case 3:
          return '수';
        case 4:
          return '목';
        case 5:
          return '금';
        case 6:
          return '토';
      }
    },
    getMealTypeName: function(mealType) {
      return Constants.MEAL_TYPE_MAP[mealType];
    },
    changeDay: function(date) {
      this.$router.push("/menu/" + this.$route.params.code + "/" + date + "/" + this.$route.params.mealType);
      this.selectedDate = this.$route.params.date;
    },
    changeMealType: function(type) {
      this.$router.push("/menu/" + this.$route.params.code + "/" + this.$route.params.date + "/" + type);
      this.selectedMealType = this.$route.params.mealType;
    }
  }
}
</script>

<style scoped>
  .card-container {
    padding: 13px;
    margin: 10px;
  }

  .card-title {
    font-size: 20px;
    font-weight: bold;
    margin-top: 14px;
  }

  .card-text {
    color: #464646;
  }

  .card-body {
    padding: 0;
  }

  .btn-group {
    margin-bottom: 0;
  }

  .alert {
    margin-top: 20px;
  }

  .btn:focus, .btn:active {
    outline: none !important;
    box-shadow: none !important;
  }
</style>



